import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Table, InputGroup, FormControl, Spinner, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';

export class DataRenderer extends Component {
  constructor(props) {
    super(props)
    this.state ={loading: false}
  }
    createGroup = async (citycode) => {
      const todayDate = new Date().toLocaleDateString('en-GB')
      await fetch(`https://sundara-api.herokuapp.com/api/getTimes?citycode=${citycode}&date=${todayDate}`)
        .then(async (response) => {
          response = await response.json();
            let group = []
            let header = []
            header.push(<thead><tr><th>Start Time</th><th>End Time</th></tr></thead>)
          for (let i = 0; i < response.goodTimes.length; i++) {
            group.push(
              <tr key={i}><td>{response.goodTimes[i][0]}</td><td>{response.goodTimes[i][1]}</td></tr>
            )
          }
          let rank = "";
          if (response.dayRanking === "Good") {
            rank = <h2 style={{color: 'green'}}>Today is a Good day <span role="img" aria-label="happy">ðŸ˜Š</span></h2>
          } else if (response.dayRanking === "Bad") {
            rank = <h2 style={{color: 'red'}}>Today is a Bad day <span role="img" aria-label="sad">ðŸ˜ž</span></h2>
          } else {
            rank = <h2>Today is an OK day.</h2>
          }
          if (response.dayRanking !== "Good") {
            let tempResp = response.dayRanking;
            let tomorrow = new Date();
            let tommdate = "";
            let i = 0
            do {
              tomorrow.setDate(tomorrow.getDate() + i);
              tommdate = tomorrow.toLocaleDateString('en-GB');
              await fetch(`https://sundara-api.herokuapp.com/api/getTimes?citycode=${citycode}&date=${tommdate}`)
              // eslint-disable-next-line
              .then(async (response) => {
                response = await response.json();
                tempResp = response.dayRanking
              })
              i += 1
            } while (tempResp !== "Good")
            const humanDate = new Date(tommdate).toDateString();
            const nextGoodDayElem = <h4><i>Next good day is: {humanDate}.</i></h4>
            this.setState({NextGoodDay: nextGoodDayElem})
          }
          this.setState({Times: group, DayRank: rank, Header: header})
      })
    }
    render() {
      return (
      <Container>
          <InputGroup className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon3">
                City GeoCoder ID
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl onChange={(event) => {
                const d = event.target.value
                this.setState({loading: true})
                setTimeout(() => { this.createGroup(d); this.setState({loading: false}); }, 750); // artificial slowdown for user experience
              }} id="basic-url" aria-describedby="basic-addon3" />
          </InputGroup>
          <ToggleButtonGroup type="radio" name="options">
            <ToggleButton value={4903279} onChange={(event) => {
                const d = "4903279"
                this.setState({loading: true})
                setTimeout(() => { this.createGroup(d); this.setState({loading: false}); }, 750); // artificial slowdown for user experience
              }}>Naperville</ToggleButton>
            <ToggleButton value={5391811} onClick={(event) => {
                const d = "5391811"
                this.setState({loading: true})
                setTimeout(() => { this.createGroup(d); this.setState({loading: false}); }, 750); // artificial slowdown for user experience
              }}>San Diego</ToggleButton>
          </ToggleButtonGroup>
          <br /><br />
        <div aria-label="Times list" key={'opts'}>
          {this.state.loading ? <Spinner animation="border" /> : <Table striped bordered hover>
          {this.state.DayRank}
          {this.state.NextGoodDay}
            {this.state.Header}
            <tbody>
              {this.state.Times}
            </tbody>
          </Table>
          }
          
        </div>
      </Container>
      );
    }
}